.project {
  position: relative;
  background-color: $second;
  overflow: hidden;
  min-height: 100vh;
}

.project-nav {
  position: fixed;
  left: 75vw;
  top: 50%;
  transform: translateY(-50%);
  z-index: 200;
  &__list {

  }
  &__item {
    cursor: pointer;
    margin-top: 5px;
    margin-bottom: 5px;
    transition: color .2s;
    display: block;
    font-size: 24px;
    @include mobileL {
      font-size: 16px;
    }
    &:hover {
      & .project-nav__link {
        color: $accent;
        mix-blend-mode: normal;
        text-decoration: line-through;
      }
    }
  }
  &__link {

  }
}

.project-imgs, .project-videos {
  position: relative;
  display: block;
}

.project-block {
  position: relative;
  width: $col6;
}

.project-block-video {
  position: relative;
  width: $col8;
}

.project-img {
  position: relative;
  width: 100%;
  max-width: 100%;
  vertical-align: middle;
  display: block;
}

.project-video {
  @extend .work__video;
  &__box {
    position: relative;
    padding-bottom: 56.25%;
    height: 0;
    overflow: hidden;
  }
}

.project-title {
  position: absolute;
  left: 50%;
  top: 0;
  width: 10.9vw;
  height: 100%;
  transform: translate(-50%);
  &--video {
    left: 66.8%;
  }
  &__box {
    width: 100%;
    height: 100%;
    transform: matrix(1, 0, 0, 1, 0, 0);
  }
  &__box2 {
    width: 100%;
    height: 100%;
    transform: matrix(1, 0, 0, 1, 0, 0);
  }
  &__name {
    font-weight: 900;
    position: absolute;
    white-space: nowrap;
    font-size: 14vw;
    line-height: 14vw;
    text-transform: uppercase;
    transform: rotate(90deg) translate(-1vw,-90%);
    transform-origin: left top;
  }
}