.about {
  position: fixed;
  top: 0;
  left: 0;
  background: white;
  min-height: 100vh;
  height: 100vh;
  width: 100%;
  z-index: 1000;
  overflow: auto;
  &__wrapper {
    display: flex;
    align-items: center;
    overflow: auto;
    height: 100%;
    @include tablet {
      flex-direction: column;
    }
  }
  &__block {
    width: 50%;
    padding: 40px;
    @include tablet {
      width: 100%;
      padding: 60px 10px;
      &:last-child {
        padding: 0;
      }
    }
  }
  &__close {
    position: absolute;
    right: 5%;
    top: 5%;
    font-size: 18px;
    @include tablet {
      font-size: 15px;
    }
    &:hover {
      color: $accent;
    }
  }
}

.about-bio {
  &__title {
    color: $accent;
    font-size: 24px;
    margin-bottom: 20px;
  }
  &__descr {
    font-size: 20px;
    margin-bottom: 15px;
  }
  &__email {
    font-size: 18px;
  }
}

.about-nav {
  &__title {
    position: absolute;
    top: -20%;
    left: 0;
    &--r {
      left: initial;
      right: 0;
    }
  }
  &__item {
    position: relative;
    width: 85%;
    &--m {
      margin-left: 15%;
    }
    & .about-nav__title:nth-child(2) {
      display: none;
      color: $accent;
    }
    &:hover {
      & .about-nav__title:first-child {
        display: none;
      }
      & .about-nav__title:nth-child(2) {
        display: block;
      }
    }
  }
  &__link {
    display: block;
  }
  &__img {
    width: 100%;
    height: 15vh;
    object-fit: cover;
  }
}