html {
  font-family: sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  color: $black;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  touch-action: manipulation;
  height: 100%;
  -ms-overflow-style: none;
  scrollbar-width: none;
}

body {
  width: 100%;
  height: 100%;
  font-family: 'Ubuntu', sans-serif;
  font-weight: 400;
  overflow-x: hidden;
}

a {
  text-decoration: none;
  color: #000;
}

img {
  max-width: 100%;
  display: block;
}

::-webkit-scrollbar {
  width: 0px;
  background: transparent;
}
::-webkit-scrollbar-thumb {
  background: #FF0000;
}

.container {
  position: relative;
  margin: 0 auto;
  width: 100%;
}

.container-inner {
  position: relative;
  max-width: 1120px;
  margin: 0 auto;
}

.container-mini {
  max-width: 900px;
  margin: 0 auto;
}

.spacer {
  position: relative;
  height: 200vh;
  width: 1px;
  left: 0;
  @include laptopM {
    height: 100vh;
  }
  @include tabletM {
    height: 100vh;
  }
}

.btn {
  background: transparent;
  border: none;
  text-transform: uppercase;
}