.home {
  position: relative;
  height: 100vh;
  width: 100%;
  top: 0;
  left: 0;
}

.home-email {
  position: absolute;
  top: calc(#{$col1} - 2vw);
  left: $col1;
  transform-origin: right top;
  transform: rotate(-90deg) translate(2px, -75px);
  font-size: 20px;
  @include mobileL {
    font-size: 8vw;
    top: 61vw;
    left: -30vw;
  }
  &__link {
    position: relative;
    display: inline-block;
  }
}

.home-descr {
  font-weight: 500;
  pointer-events: none;
  position: absolute;
  font-size: 2.6vw;
  line-height: 2.5vw;
  text-transform: none;
  white-space: nowrap;
  top: 30.4vh;
  top: calc(var(--vh,1vh)*30.4);
  left: calc(33.33333vw + 40px);
  &-main {
    &__line {

    }
    &__lr {
      
    }
  }
}

.big-text {
  position: absolute;
  z-index: 10;
  &__block {
    transform: matrix(1, 0, 0, 1, 0, 0);
  }
  &__word {
    position: absolute;
    top: 0;
    left: 0;
    font-family: sans-serif;
    font-style: normal;
    font-weight: 900;
    font-size: 15vw;
    line-height: 10vw;
    text-transform: uppercase;
    pointer-events: none;
    color: $accent;
    &--1 {
      transform: rotate(90deg) translateY(-75.1vw);
      transform-origin: top left;
      margin-top: 14.6vh;
      margin-top: calc(var($vh,1vh)*14.6);
    }
    &--2 {
      transform: rotate(90deg) translateY(-87.6vw);
      transform-origin: top left;
      margin-top: 56.5vh;
      margin-top: calc(var($vh,1vh)*56.5);
    }
  }
}

.home-projects, .home-last-projects {
  position: relative;
  margin-bottom: 50px;
  &__title {
    text-align: center;
    font-size: 10vw;
    margin-bottom: 30px;
  }
}

.home-projects-nav {
  display: flex;
  margin-bottom: 30px;
  &__box {
    position: relative;
    width: $col4;
    min-width: $col4;
    &:hover .home-projects-nav__name {
      transform: translateY(1rem);
    }
  }
  &__link {
    position: relative;
    display: block;
    font-size: 16px;
    &:before {
      position: absolute;
      top: 0;
      left: 0;
      content: '';
      background: $accent;
      width: 100%;
      height: 100%;
      opacity: .7;
    }
  }
  &__name {
    text-align: center;
    color: $accent;
    font-size: 34px;
    font-weight: 900;
    text-transform: uppercase;
    margin-bottom: 16px;
    transition: transform .5s;
    @include tablet {
      font-size: 16px;
    }
  }
  &__img {
    position: relative;
    height: 70vh;
    width: 100%;
    object-fit: cover;
    &:hover {
      mix-blend-mode: multiply;
    }
  }
}

.home-video {
  &__box {
    position: absolute;
    pointer-events: none;
    width: $col5;
    height: 23.4375vw;
    bottom: 0;
    left: $col1;
    overflow: hidden;
  }
  &__video {
    height: 100%;
  }
}