.preloader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: $white;
  z-index: 1000;
  &__text {
    position: fixed;
    font-family: sans-serif;
    top: 50%;
    left: 50%;
    transform: translate(-50%);
    font-weight: 300;
    text-transform: uppercase;
  }
  &__dots-container {
    position: absolute;
    left: 100%;
    bottom: -8%;
    display: flex;
    justify-content: center;
    align-items: center;
    animation-delay: 1s;
  }
}

@for $i from 1 through 3 {
  .dots-#{$i} {
    width: 3px;
    height: 3px;
    background: #f583a1;
    border-radius: 50%;
    @include anim();
    background-color: $black;
    margin: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    &:before {
      content: '';
      width: 3px;
      height: 3px;
      border-radius: 50%;
      background-color: $accent;
      opacity: 0.7;
      animation: scale 2s infinite cubic-bezier(0, 0, 0.49, 1.02);
      animation-delay: 200ms * $i;
      transition: 0.5s all ease;
      transform: scale(1);
    }
  }
}