.works {
  position: relative;
  margin-bottom: 100px;
}

.work {
  position: relative;
  @include tablet {
    margin-bottom: 40px;
  }
  &--0 {
    width: $col5;
  }
  &--1 {
    width: $col4;
    left: $col5;
    margin-top: -$col1;
    @include tablet {
      width: $col7;
      left: $col1;
      margin-top: 0;
    }
  }
  &--2 {
    width: $col3;
    left: $col9;
    margin-top: -1.5vw;
    @include tablet {
      width: $col6;
      left: $col6;
      margin-top: 0;
    }
  }
  &--3 {
    width: $col3;
    left: $col1;
    margin-top: -12.5vw;
    @include tablet {
      width: $col5;
      left: $col2;
      margin-top: 0;
    }
  }
  &--4 {
    width: $col4;
    left: $col4;
    margin-top: -$col2;
    @include tablet {
      width: $col7;
      left: $col5;
      margin-top: 0;
    }
  }
  &__box {
    display: block;
    width: inherit;
    cursor: pointer;
    &:hover {
      & .work__img {
        z-index: 2;
        transform: matrix(1.1, 0, 0, 1.1, 0, 0);
      }
    }
  }
  &__mask {
    position: relative;
    pointer-events: none;
    display: block;
    overflow: hidden;
  }
  &__img {
    position: relative;
    display: inline-block;
    max-width: 100%;
    vertical-align: middle;
    cursor: pointer;
    z-index: -2;
    transition: all .2s;
  }
  &__text {
    font-weight: 500;
    position: relative;
    display: inline-block;
    font-size: 2.07vw;
    line-height: 2vw;
    text-transform: uppercase;
    cursor: pointer;
    pointer-events: none;
    @include tabletL {
      font-size: 16px;
      line-height: 20px;
    }
    &--1 {
      position: absolute;
      top: 0;
      right: 0;
      transform: rotate(90deg) translate(calc(100% - 3px),calc(-100% - 4px));
      transform-origin: top right;
      @include tabletL {
        transform: rotate(90deg) translate(calc(100% - 2px),calc(-100% - 4px));
      }
    }
  }
  &__name {

  }
  &__video-box {
    position: relative;
    padding-bottom: 56.25%;
    padding-top: 30px;
    height: 0;
    overflow: hidden;
  }
  &__video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}