.grid {
  position: fixed;
  pointer-events: none;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 1;
  transform: translateZ(0);
  &--accent {
    & .grid__line {
      background-color: $accent;
    }
  }
  &__line {
    background-color: $black;
    &--h1, &--h2 {
      position: absolute;
      height: 1px;
    }
    &--h1 {
      top: 33.3333vh;
      top: calc(var($vh,1vh)*33.3333);
      width: 100%;
    }
    &--h2 {
      top: 66.6666vh;
      top: calc(var($vh,1vh)*66.6666);
      width: 100vw;
    }
    &--v1, &--v2 {
      position: absolute;
      height: 100vh;
      height: calc(var($vh,1vh)*100);
      width: 1px;
    }
    &--v1 {
      left: $col4;
    }
    &--v2 {
      left: $col8;
    }
  }
}

.grid-mini {
  pointer-events: none;
  width: 100%;
  left: 0;
  opacity: 1;
  background: $gradient-grid;
  &--accent {
    & .grid-mini__line {
      background-color: $accent;
    }
  }
  &, &__line {
    position: fixed;
    height: 100%;
    top: 0;
    z-index: -1;
  }
  &__line {
    background-color: $black;
    opacity: .1;
    width: 1px;
    &--1 {
      left: $col1;
    }
    &--2 {
      left: $col2;
    }
    &--3 {
      left: $col3;
    }
    &--5 {
      left: $col5;
    }
    &--6 {
      left: $col6;
    }
    &--7 {
      left: $col7;
    }
    &--9 {
      left: $col9;
    }
    &--10 {
      left: $col10;
    }
    &--11 {
      left: $col11;
    }
  }
}