.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 30;
  &__wrapper {
    // height: 20px;
    // width: 83.5%;
    // position: absolute;
    // top: 20px;
    // left: 50%;
    // transform: translateX(-50%);
    // background: $second;
    // opacity: .9;
  }
  &-nav {
    position: absolute;
    font-size: 18px;
    text-transform: uppercase;
    top: 20px;
    left: calc(#{$col1} + 40px);
    @include mobileL {
      font-size: 16px;
      left: 8.33333vw;
    }
    &__list {

    }
    &__item {
      float: left;
      margin-right: 20px;
      cursor: pointer;
      transition: color .3s;
      @include mobileL {
        margin-right: 35px;
      }
      &:last-child {
        margin-right: 0;
        margin-left: 40px;
        @include mobileL {
          margin-left: 24px;
        }
      }
    }
    &__link {
      &:hover {
        text-decoration: line-through;
        color: $accent;
      }
      &--active {
        color: red;
        mix-blend-mode: normal;
        text-decoration: line-through;
      }
    }
  }
  &-dropdown {
    &:hover &__list {
      display: block;
    }
    &__list {
      display: none;
    }
    &__item {

    }
    &__link {

    }
  }
}